/* ScrollPicker Styles */
.scroll-picker-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f8f9fa;
  border-radius: 20px;
  padding: 20px 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e9ecef;
  min-height: 300px;
  position: relative;
}

.picker-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.picker-icon {
  font-size: 1.5rem;
  margin-bottom: 8px;
  color: #6c757d;
}

.picker-label-group {
  text-align: center;
}

.picker-label {
  font-size: 1rem;
  font-weight: 600;
  color: #495057;
  margin-bottom: 4px;
}

.picker-unit {
  font-size: 0.85rem;
  color: #6c757d;
}

.scroll-picker {
  width: 100%;
  overflow: hidden;
  position: relative;
  cursor: grab;
  user-select: none;
}

.scroll-picker:active {
  cursor: grabbing;
}

.picker-items {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.picker-item {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  font-weight: 600;
  color: #adb5bd;
  transition: all 0.3s ease;
  cursor: pointer;
  width: 100%;
}

.picker-item.selected {
  color: #e91e63;
  font-size: 2.2rem;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(233, 30, 99, 0.3);
}

.picker-item:hover {
  color: #6c757d;
}

.picker-selection-indicator {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 50px;
  border: 2px solid #e91e63;
  border-radius: 15px;
  background: rgba(233, 30, 99, 0.1);
  pointer-events: none;
  z-index: 1;
}

/* Î™®Î∞îÏùº ÏµúÏ†ÅÌôî */
@media (max-width: 768px) {
  .scroll-picker-container {
    padding: 15px 10px;
    min-height: 280px;
  }
  
  .picker-item {
    font-size: 1.6rem;
  }
  
  .picker-item.selected {
    font-size: 2rem;
  }
  
  .picker-selection-indicator {
    height: 45px;
  }
}

/* ÌÑ∞Ïπò ÎîîÎ∞îÏù¥Ïä§ ÏµúÏ†ÅÌôî */
@media (hover: none) and (pointer: coarse) {
  .scroll-picker-container {
    padding: 20px 15px;
  }
  
  .picker-item {
    font-size: 1.8rem;
    padding: 10px 0;
  }
  
  .picker-item.selected {
    font-size: 2.2rem;
  }
}

/* Îã§ÌÅ¨ Î™®Îìú ÏßÄÏõê */
@media (prefers-color-scheme: dark) {
  .scroll-picker-container {
    background: #2d3748;
    border-color: #4a5568;
  }
  
  .picker-label {
    color: #e2e8f0;
  }
  
  .picker-unit {
    color: #a0aec0;
  }
  
  .picker-item {
    color: #718096;
  }
  
  .picker-item.selected {
    color: #e91e63;
  }
  
  .picker-selection-indicator {
    border-color: #e91e63;
    background: rgba(233, 30, 99, 0.2);
  }
}
